<template>
  <div>
    <v-row no-gutters>
      <v-col :cols="side_blank"/>
        <v-row no-gutters class="flex-nowrap">
          <h1 class="my-9 pt-2 primary--text"> ▏{{road[road_idx].title}}</h1>
          <h1 class="my-9 pt-2">&nbsp;{{main_subtitle(is_road())}}</h1>
        </v-row>
    </v-row>
    <v-row no-gutters class="flex-nowrap">
      <v-col :cols="side_blank"></v-col>
          <!-- 지도 or 거점 사진-->
          <v-card v-on:click="openMap(is_road())" class="mr-5">
            <v-img :src="match_image(is_road())" width="930" max-height="1000"/>
          </v-card>
          <!-- 거점 정보 네비게이션 -->
          <v-card width="320" class="blue lighten-5" elevation="0">
            <v-card-title>
              <p class="font-weight-black pb-3 pt-5 pl-3 mb-0 text-h4 primary--text"> ▏</p><p class="font-weight-medium pb-3 pt-5 mb-0 text-h4">거점정보</p>
            </v-card-title>
            <v-expansion-panels accordion flat mandatory :value="pageidx()">
              <v-expansion-panel class="cut blue lighten-5" v-for="road in road_nav" :key="road.title" @click="click(road.id)">
                <v-expansion-panel-header>
                  <p class="mt-3 mb-2 text-h6 primary--text font-weight-bold">>> &nbsp;{{road.title}}</p>
                </v-expansion-panel-header>
                <v-expansion-panel-content v-for="(spot,index) in road.spots" :key="index">
                  <v-btn class="pl-15" block tile text depressed :to="{name:'story_spot',params:{road_id:$route.params.road_id,spot_id:index+1}}"> {{ spot }}
                    <v-spacer></v-spacer>
                  </v-btn>
                </v-expansion-panel-content>
              </v-expansion-panel>
              <v-spacer class="my-2">&nbsp;</v-spacer>
            </v-expansion-panels>
          </v-card>
          <!-- 거점 정보 네비게이션 끝-->
          <v-spacer></v-spacer>
    </v-row>
    <!-- 길 및 거점 정보 표시 -->
    <router-view :road_data="road[road_idx]" :side_blank="side_blank" class="my-10"></router-view>
    
  </div>
</template>

<script>
import datafile from "@/data/StoryData.js";
export default {
  mounted: function(){
    // const map= this.$refs.maps.map
  },
  methods:{
    click:function(road_id){
      if(this.$route.params.road_id != road_id){
        this.$router.push({path:`/story/road/${road_id}`})
      }
    },
    openMap:function(){
      let url = this.$router.resolve({name:'DynamicMap'}).href;
      window.open(url);
    },
    pageidx:function(){
      this.road_idx = this.$route.params.road_id-1 // mapping id(starting from 1) to index(starting from 0)
      return this.road_idx;
    },
    is_road: function(){
      return isNaN(this.$route.params.spot_id)
    },
    match_image: function(isRoad){ // for image
      let current_road = this.road[this.road_idx]
      if(!isRoad){ // if it is not spot page
        return current_road.spots[this.$route.params.spot_id-1].map_img //return road[road index].spot[spot index] map image
        }
      return current_road.map_img // return road map image
    },
    main_subtitle: function(isRoad){ // for the top title
      let current_road = this.road[this.road_idx]
      if(!isRoad){
        return `${current_road.subtitle} > ${current_road.spots[this.$route.params.spot_id-1].title}`
        }
      return current_road.subtitle
    }
  },
  data() {
    return {
      road_idx:0, //declare variable with default fallback value
      road: datafile.road,  //gets road data from StoryData.js
      road_nav: datafile.road_nav,  //gets navigator data from StoryData.js
      side_blank:2, // side blank size in cols
    };
  },
};
</script>


<style lang="scss" scoped>
@import '@/scss/variables.scss';
.cut{
   padding:$expansion-panel-content-padding;
}
</style>
